<template>
  <nav>
    <div class="nav-wrapper green">
      <div class="container nav-container">
        <div class="left-nav">
          <router-link to="/" class="title">Employee Manager</router-link>
        </div>
        <div class="right-nav">
          <ul class="right">
            <li v-if="isLoggedIn">
              <span class="email black-text">{{currentUser}}</span>
            </li>
            <li v-if="isLoggedIn">
              <router-link to="/">Dashboard</router-link>
            </li>
            <li v-if="!isLoggedIn">
              <router-link to="/login">Login</router-link>
            </li>
            <li v-if="!isLoggedIn">
              <router-link to="/register">Register</router-link>
            </li>
            <li v-if="isLoggedIn">
              <button v-on:click="logout" class="btn grey">Logout</button>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </nav>
</template>

<style>
.nav-container {
  display: flex;
  flex-direction: row;
}
.left-nav {
  align-items: flex-start;
  width: 50%;
}
.right-nav {
  align-items: flex-end;
  width: 50%;
}

.title {
  font-size: 26px;
  font-weight: 600;
}
</style>

<script>
import firebase from "firebase";
export default {
  name: "navbar",
  data() {
    return {
      isLoggedIn: false,
      currentUser: false
    };
  },
  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
  methods: {
    logout: function() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.go({ path: this.$router.path });
        });
    }
  }
};
</script>


<style scoped>
.email {
  padding-right: 10px;
}
</style>





